import React, { useState, useEffect } from 'react';

const ClickList = ({ data, families, callback }) => {
    const makeFontSearch = (e) => {
      let s = e.target.value;
      let r = new RegExp(s, 'i');
      //console.log(s, r);
      setList(data.filter(o => r.test(o.name)))
    }

    let [list, setList] = useState([]);
    useEffect(() => {
      setList(data);
    }, [data]);
  
    return (<>
      <div className="select-intro flex-xs-parent p-xs-vm p-md-vm">
        <h3>Font&nbsp;library</h3>
        <input className="searchbar" type="search" placeholder="SEARCH FONT" onChange={makeFontSearch} />
      </div>
      <section className="flex-xs-parent flex-xs-wrap font-library">
        {list.map((o, i) => (
            <FontCard
              index={i}
              object={o}
              families={families}
              callback={callback}
            />
        ))}
      </section>
    </>
    );
  }

const FontCard = ({index, object, families, callback}) => {

  let [rLetter,setRLetter] = useState("Aa");
  let letters = 'abcdefghijklmnopqrstuvwxyz';

  console.log(object);
  
  useEffect(() => {
    setRLetter( [...Array(2)].map(i=>letters[Math.random()*letters.length|0]).join`` );
	}, [letters]);

  return(<>
          <div className="font-card" key={index}>
              <section className="grid">
                <h4 className="col-xs-12" style={{ fontFamily: object.name }}>{object.name}</h4>
              <h5 className="col-xs-12 p-xs-txs">{object.styles}</h5>
              </section>
                <div className="font-card-btns-container flex-xs-parent flex-xs-justify-end font-card-nav">
                  <div className={`font-card-btns flex-xs-parent ${object.id === families.first.id ? 'chosen' : ''}`} onClick={() => callback({ first: object })}>Header</div>
                  <div className={`font-card-btns flex-xs-parent ${object.id === families.second.id ? 'chosen' : ''}`} onClick={() => callback({ second: object })}>Body</div>
                </div>
            <h4 className="font-card-dummy" style={{ fontFamily: object.name }}>{rLetter}</h4>
          </div>
  </>);
}



export { ClickList };