import React, { useState, useEffect } from 'react';

const RollingHeader = ({children}) => {
    return (
	<h2 style={{width: '100%', height: '25vw', fontSize: '20vw', color: '#0018ba', position: 'relative', display: 'block', letterSpacing: '-7px', cursor: 'default'}}>
        {/* <span style={{height: '10em'}}>.</span> */}
        {children.map((o,i)=>(
            <span style={{position: 'relative', display: 'inline'}} key={i}>{o}</span>
        ))}
	</h2>
	);
}

const HeaderLetter = ({letter, fonts}) => {
    let [fontFamily, setFontFamily] = useState('');
    // let [hoverActive, setHoverActive] = useState(false);

    useEffect(() => {
        if (!fonts.fonts.length) return;
            let f = fonts.fonts;
            let interval = Math.floor(Math.random() * 3000) + 2000;
            let r1 = Math.floor(Math.random() * f.length);
            setFontFamily(f[r1]);
            const randomFont = setInterval(() => {
                let r1 = Math.floor(Math.random() * f.length);
                setFontFamily(f[r1]);
            }, interval);
            return () => clearInterval(randomFont);
        }, [fonts]);
      
    return (<>
    <span 
        style={{ fontFamily: fontFamily.name, position: 'aboslute', height: '400px'}}
        // onMouseEnter={()=>setHoverActive(true)} 
        // onMouseLeave={() => setHoverActive(false)}
    >
        {letter}
	</span>
    {/* <p 
        className={`${hoverActive?'hide':''}`}
        style={{ fontFamily: fontFamily.name, position: 'aboslute'}}
    >
        {fontFamily.name}
    </p> */}
	</>);
}

  export {RollingHeader, HeaderLetter};
