import React from 'react';

import Star from "../assets/images/star.svg";

const Shuffle = ({callback, fontFamilies, fonts}) => {

    let r1 = Math.floor(Math.random() * fonts.length);
    let r2 = Math.floor(Math.random() * fonts.filter((o, i) => i !== r1).length);

    return (
	    <section className="shuffle-section p-xs-vl p-xs-hm">
            <div className="p-xs-bxxl">
                <h6>Header</h6>
                <h3 style={{ fontFamily: fontFamilies.first.name }}>{fontFamilies.first.name}</h3>
            </div>
            <button className="shuffle-btn" type="button" onClick={() => callback({ first: fonts[r1], second: fonts[r2]})}>Shuffle us</button>
            <div className="p-xs-txxl">
                <h6>Body</h6>
                <h3 style={{ fontFamily: fontFamilies.second.name }}>{fontFamilies.second.name}</h3>
            </div>
            <img src={Star} className="star" alt="shuffle-star"/>
	    </section>
	);
}

export {Shuffle};