import React, { useReducer, useState, useEffect } from 'react';
import {Link} from 'react-router-dom';
import {
	Switch,
	Route,
	useRouteMatch
} from 'react-router-dom';

import { renderToString } from "react-dom/server";
import jsPDF from "jspdf";

import { fontFamiliesData, fontSizesData, spacingsData } from "../pairingdoc-data";
import { Spacing } from "../components/spacing";
import { PairingDoc } from "../components/pairing-doc";
import { ClickList } from "../components/click-list";
import { FontRange } from "../components/font-range";
import { PairNav } from "../components/pair-nav";
import { Shuffle } from "../components/shuffle";
import { Button } from "../components/button";

import gearIcon from '../assets/images/icon_settings.svg'; 

const reduceProperty = (s, a) => {
	s[+a.i] = { ...s[+a.i], ...a.v };
	return [...s];
}
const reduceIndex = (s, a) => ({ ...s, ...a });

//Here is home content. 
const PairPage = ({ fonts }) => {
	let { path } = useRouteMatch();

	let [fontFamilies, setFontFamilies] = useReducer(
		reduceIndex,
		fontFamiliesData
	);

	let [fontSizes, setFontSizes] = useReducer(
		reduceProperty,
		fontSizesData
	);

	let [spacings,setSpacings] = useReducer(
		(s,a) => {
			s[+a.i]={...s[+a.i],...a.v};
			return [...s];
		},
		spacingsData
	);

	useEffect(() => {
		if (!fonts.length) return;
		let r1 = Math.floor(Math.random() * fonts.length);
		let r2 = Math.floor(Math.random() * fonts.filter((o, i) => i !== r1).length);
		// console.log(r1, r2, fonts[r1], fonts[r2]);
		setFontFamilies({ first: fonts[r1] });
		setFontFamilies({ second: fonts[r2] });
	}, [fonts]);

	let [sideBarOpen, setSideBarOpen] = useState(true);

	const printDoc = e => {
		e.preventDefault();
		const string = renderToString(
			<PairingDoc
				fontFamilies={fontFamilies}
				fontSizes={fontSizes}
				spacings={spacings}
			/>
		);
		const pdf = new jsPDF("p", "mm", "a4");

		pdf.fromHTML(string);
		pdf.save("pdf");
	}

	return (<>
		<article className="container">
			<section className="grid">
				<div className="closeBtn position-xs-f display-md-n" onClick={()=>setSideBarOpen(!sideBarOpen)}>
					<img src={gearIcon} alt=""></img>
				</div>
				<Switch>
					<Route exact path={`${path}`}>
						<PairNav path={path}>
							<Link className="flex-xs-parent flex-xs-vertical hover-underline color-blue" to={`${path}/`}>Choose font</Link>
							<Link className="flex-xs-parent flex-xs-vertical hover-underline" to={`${path}/set-sizes`}>Font size</Link>
							<Link className="flex-xs-parent flex-xs-vertical hover-underline" to={`${path}/spacing`}>Spacing</Link>
						</PairNav>
						<Button linkLocation={`${path}/set-sizes`} text='next'></Button>
						<div className={`selection col-sm-12 col-md-5 position-xs-a position-md-r ${sideBarOpen?'active':''}`}>
						<Shuffle 
							fontFamilies={fontFamilies}
							callback={setFontFamilies}
							fonts={fonts}
							/>
						<PairFont
							path={path}
							spacings={spacings}
							fontlist={fonts}
							setFontFamilies={setFontFamilies}
							fontFamilies={fontFamilies}
							fontSizes={fontSizes}
						/>
						</div>
					</Route>
					<Route path={`${path}/set-sizes`}>
						<PairNav path={path}>
							<Link className="flex-xs-parent flex-xs-vertical hover-underline" to={`${path}/`}>Choose font</Link>
							<Link className="flex-xs-parent flex-xs-vertical hover-underline color-blue" to={`${path}/set-sizes`}>Font size</Link>
							<Link className="flex-xs-parent flex-xs-vertical hover-underline" to={`${path}/spacing`}>Spacing</Link>
						</PairNav>
						<Button linkLocation={`${path}/spacing`} text='next'></Button>
						<div className={`selection col-sm-12 col-md-5 position-xs-a position-md-r ${sideBarOpen?'active':''}`}>
							<Shuffle 
							fontFamilies={fontFamilies}
							callback={setFontFamilies}
							fonts={fonts}
							/>
							<SetSize
							path={path}
							spacings={spacings}
							setFontSizes={setFontSizes}
							fontFamilies={fontFamilies}
							fontSizes={fontSizes}
							/>
						</div>
					</Route>
					<Route path={`${path}/spacing`}>
						<Button linkLocation={`${path}/spacing`} text='download'></Button>
						<PairNav path={path}>
							<Link className="flex-xs-parent flex-xs-vertical hover-underline" to={`${path}/`}>Choose font</Link>
							<Link className="flex-xs-parent flex-xs-vertical hover-underline" to={`${path}/set-sizes`}>Font size</Link>
							<Link className="flex-xs-parent flex-xs-vertical hover-underline color-blue" to={`${path}/spacing`}>Spacing</Link>
						</PairNav>
						<div className={`selection col-sm-12 col-md-5 position-xs-a position-md-r ${sideBarOpen?'active':''}`}>
							<Shuffle 
							fontFamilies={fontFamilies}
							callback={setFontFamilies}
							fonts={fonts}
							/>
							<SetSpacing
								path={path}
								spacings={spacings}
								setSpacings={setSpacings}
								fontFamilies={fontFamilies}
								fontSizes={fontSizes}
							/>
						</div>
					</Route>
				</Switch>
				<div className="preview col-xs-12 offset-xs-0 col-md-8 offset-md-6 pairing-wrapper p-xs-hm">
					<PairingDoc
						fontSizes={fontSizes}
						fontFamilies={fontFamilies}
						spacings={spacings}
					/>
				</div>
			</section>
		</article>
	</>);
}

//Here is the start pairing content.
//If this is the flow we want to go for. The flow is accesable from the home screen. 

const PairFont = ({ fontlist, setFontFamilies, fontFamilies }) => {
	return (
			<section className="p-xs-hm">
				<ClickList
					data={fontlist}
					families={fontFamilies}
					callback={setFontFamilies}
				/>
			</section>
	);
}

const SetSize = ({ setFontSizes, fontSizes }) => {

	const setSize = e => {
		e.preventDefault();
		setFontSizes({
			i: e.target.dataset.id,
			v: { size: e.target.value }
		});
	}

	return (
			<section className="p-xs-hm">
				<div className="select-intro flex-xs-parent p-xs-vm p-md-vm">
					<h3>Set sizing</h3>
				</div>
				{fontSizes.map((o, i) => (
					<FontRange
						key={i}
						id={i}
						size={fontSizes}
						setSize={setSize} />
				))}
			</section>
	);
}

const SetSpacing = ({ setSpacings, spacings }) => {

	const setTracking = e => {
		e.preventDefault();
		setSpacings({
			i: e.target.dataset.id,
			v: { trackingSize: e.target.value }
		});
	}

	const setLeading = e => {
		e.preventDefault();
		setSpacings({
			i: e.target.dataset.id,
			v: { leadingSize: e.target.value }
		});
	}


	return (
			<section className="p-xs-hm">
				<div className="select-intro flex-xs-parent p-xs-vm p-md-vm">
					<h3>Set spacing</h3>
				</div>
				{spacings.map((o, i) => (
					<Spacing
						key={i}
						id={i}
						spacing={spacings}
						setTracking={setTracking}
						setLeading={setLeading}
					/>
				))}
			</section>
	);
}


export default PairPage;