import React, { useReducer, useEffect } from 'react';
import { fontFamiliesData, fontSizesData, spacingsData } from "../pairingdoc-data";

import {PairingDoc} from "../components/pairing-doc";
import {RollingHeader, HeaderLetter} from "../components/header";
import {Button} from "../components/button";


const IntroPage = (fonts) => {
    const reduceIndex = (s, a) => ({ ...s, ...a });
    
    let [fontFamilies, setFontFamilies] = useReducer(
          reduceIndex,
          fontFamiliesData
    );

    useEffect(() => {
      if (!fonts.fonts.length) return;
        let f = fonts.fonts;
          let r1 = Math.floor(Math.random() * f.length);
          let r2 = Math.floor(Math.random() * f.filter((o, i) => i !== r1).length);
          setFontFamilies({ first: f[r1] })
          setFontFamilies({ second: f[r2] })
    }, [fonts]);
    
    return (<>
		<Button linkLocation='pair-fonts' text='start'></Button>
		<section className='grid p-xs-hm'>
			<h1 className="max-xs-s p-xs-tl col-md-6 col-xs-10">Experiment with font pairing and styling options while seeing the results live.</h1>
		</section>
		<section className='p-xs-hm p-xs-vxl'>
			<RollingHeader fonts={fonts}>
				<HeaderLetter
					letter='p'
					fonts={fonts}
				/>
				<HeaderLetter
					letter='a'
					fonts={fonts}
				/>
				<HeaderLetter
					letter='i'
					fonts={fonts}
				/>
				<HeaderLetter
					letter='r'
					fonts={fonts}
				/>
				<HeaderLetter
					letter='S'
					fonts={fonts}
				/>
				<HeaderLetter
					letter='p'
					fonts={fonts}
				/>
				<HeaderLetter
					letter='a'
					fonts={fonts}
				/>
				<HeaderLetter
					letter='c'
					fonts={fonts}
				/>
				<HeaderLetter
					letter='e'
					fonts={fonts}
				/>
			</RollingHeader>
		</section>
      <section className="grid p-xs-hm ">
        <div className="container p-xs-txl p-xs-rs col-md-5 col-xs-10">
            <h3>Choose a body and header font to pair, and then customize each with styling options. Once you are complete with the process you will have the option to download a personal style-guide.</h3>
        </div>
        <div className="container m-xs-txl col-xs-7 home-preview p-xs-hm">
          <PairingDoc 
			fontSizes={fontSizesData} 
			fontFamilies={fontFamilies}
			spacings={spacingsData}
			/>
        </div>
      </section>
    </>);
  };

  export default IntroPage;
